.card {
  background: #3b945f;
  min-height: 320px;
  margin: 7px;
  margin-top: 20px;
  width: 230px;
  color: black;
  align-self: center;
  transition: 0.3s;
  object-fit: scale-down;
}

.card:hover {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3), 0 3px 6px rgba(0, 0, 0, 0.3);
}

.card > .img-container {
  overflow: hidden;
  text-align: center;
}
.card > .img-container > img {
  height: 160px;
  width: 100%;
  transition: 0.3s;
}

.card > .img-container > img:hover {
  transform: scale(1.1);
}

.card > .content {
  text-align: center;
}

p {
  font-family: "Song Myung", serif;
  font-size: 20px;
  margin: 1px;
}


.flippy-back{
 max-height: 400px;
}

/* Back of card */

.back-container {
  height: 100%;
  color: white;
}

.toggle-style {
  font-family: "Song Myung", serif;
  color: black;
  text-align: left;
  height: 100%;
  width: 100%;
  background: none;
  border: none;
  text-decoration: underline;
  cursor: pointer;
}

.toggle-style:hover{ 
  color: #069;
}
.description {
  font-size: 14px;
  margin-top: 10px;
  height:50%;
  text-align: left;
}
.water-sun{
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  margin-bottom: 12px;
}
